<template>
	<view class="content">
		<!-- From Uiverse.io by kamehame-ha -->
		<div class="cards">
			<div class="card green" @click="topage1()">
				<p class="tip">订单确认</p>
				<p class="second-text">Order Confirm</p>
			</div>
			<div class="card green" @click="topage2()">
				<p class="tip">订单查询</p>
				<p class="second-text">Order Search</p>
			</div>
			<div class="card green" @click="topage3()">
				<p class="tip">报表</p>
				<p class="second-text">Statement</p>
			</div>
		</div>
	</view>
</template>

<script>
export default {
	data() {
		return {
			num: 0  // 初始化 num 变量
		};
	},
	onLoad(parms) {
		uni.getStorage({
			key: 'userData',
			success: (res) => {
			},
			fail: () => {
				uni.reLaunch({
					url: '/pages/login/login'
				});
			}
		});
		let shareInfo = {};
		shareInfo.title = '新兴移动端';
		shareInfo.path = 'pages/index/index';
		this.setShareInfo(shareInfo);
	},
	onUnload() { },
	methods: {
		topage1() {
			uni.navigateTo({
				url: '/pages/confirm/index'
			});
		},
		topage2() {
			uni.navigateTo({
				url: '/pages/inquiry/index'
			});
		},
		topage3() {
			uni.navigateTo({
				url: '/pages/statement/index'
			});
		}
	},
}
</script>

<style scoped>
.content {
	padding: 50rpx;

}

/* From Uiverse.io by kamehame-ha */
.cards {
	display: flex;
	flex-direction: column;
	gap: 15px;
}

/* 卡片颜色 */
.cards .red {
	background-color: #f43f5e;
}

.cards .blue {
	background-color: #3b82f6;
}

.cards .green {
	background-color: #43C6AC;
}

/* 卡片基础样式 */
.cards .card {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	text-align: center;
	height: 200rpx;
	width: 100%;
	border-radius: 10px;
	color: white;
	cursor: pointer;
	transition: 400ms;
}

/* 卡片文字样式 */
.cards .card p.tip {
	font-size: 1em;
	font-weight: 700;
}

.cards .card p.second-text {
	font-size: 0.7em;
}

/* 定义点击动画 */
@keyframes clickAnimation {
	0% {
		transform: scale(1.04);
		background-color: #11998e;
	}

	50% {
		transform: scale(1.04);
	}

	100% {
		transform: scale(1.05);
	}
}

/* 点击时触发动画 */
.cards .card:active {
	animation: clickAnimation 0.2s ease-in-out;
}
</style>